.experience_container{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
}
.experience_container>div{
    background: var(--color-bg-variant);
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
}
.experience_container>div:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}
.experience_container h3{
    font-size: 1.5rem;
    color: var(--color-primary);
    text-align: center;
    margin-bottom: 2rem;
}
.experience_content{
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 2rem;
}
.experience_details{
    display: flex;
    gap: 1rem;
    column-gap: 1rem;
}
.experience-details-icon{
    margin-top: 6px;
    color: var(--color-primary);
}
@media screen  and (max-widht: 1024px){
    .experience_container{
        grid-template-columns: 1fr;
    }
    .experience_container>div{
        width: 80%;
        padding: 2rem;
        margin: 0 auto;
    }
    .experience_content{
        padding: 1rem;
    }
}
@media screen  and (max-widht: 600px){
    .experience_container{
        padding: 1rem;
    }
    .experience_container>div{
        width: 100%;
        padding: 2rem 1rem;
        margin: 0 auto;
    }
    .experience_content{
        padding: 1rem;
    }
}
